import React, { useRef, useState } from "react"
//@ts-ignore
import { MathUtils, Mesh, RepeatWrapping, Texture, DoubleSide } from "three";

// import react-three-fiber
import { useFrame, useLoader, extend, useThree } from "react-three-fiber"

//@ts-ignore
import { TextureLoader } from 'three/src/loaders/TextureLoader';
//@ts-ignore
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
//@ts-ignore
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';

import { SceneProps } from "./scene_props";


//@ts-ignore
import liquidGold from "../../images/textures/liquid-gold-texture.jpeg";
// import fontRobotoRegular from "./Roboto_Regular.json";
// import fontGentilisRegular from "three/examples/fonts/gentilis_regular.typeface.json";
// import fontHelivetikerRegular from "three/examples/fonts/helvetiker_regular.typeface.json";
//@ts-ignore
import fontOptimerRegular from "three/examples/fonts/optimer_regular.typeface.json";


extend({ FontLoader })
extend({ TextGeometry })

const degToRad = MathUtils.degToRad
const radToDeg = MathUtils.radToDeg
// const colors = ['#21242d', '#ea5158', '#0d4663', '#ffbcb7', '#2d4a3e', '#8bd8d2']

const textures: any = {
  // 
};

function getTexture(name: string = ''): Texture {
  if(!name) name = 'liquidgold';

  if (name == 'liquidgold' && !textures['liquidgold']) {
    // // create texture
    const liquidGoldTexture = useLoader(TextureLoader, liquidGold);
    liquidGoldTexture.wrapS = RepeatWrapping;
    liquidGoldTexture.wrapT = RepeatWrapping;
    liquidGoldTexture.repeat.set(1, 1);
    textures['liquidgold'] = liquidGoldTexture;
  }

  return textures[name.toLowerCase().replace(/\W+/g, '')] || null;
}

export function TextMesh(props: any) {
  const [hovered, setHover] = useState(false);
  const [active, setActive] = useState(false)

  const mesh: Mesh = useRef();

  // actions to perform in current frame
  useFrame(() => {
    if (props.autoRotate && !hovered) {
      //mesh.current.rotation.x = mesh.current.rotation.y += 0.01;
      const roY = Math.abs(radToDeg(mesh.current.rotation.y) % 360);
      //console.log(roY);
      //console.log(roY, mesh.current.rotation.y);
      mesh.current.rotation.y = degToRad(roY > 45 && roY < 333 ? Math.round(roY + 1): roY + 0.1);
    }
    mesh.current.geometry.center();
  });

  // create lava texture
  // const lava_texture = useLoader(THREE.TextureLoader, lava);
  // lava_texture.wrapS = THREE.RepeatWrapping;
  // lava_texture.wrapT = THREE.RepeatWrapping;
  // lava_texture.repeat.set(0.05, 0.05);

  const material = {
    texture: null,
    color: props.color,
    defaultTexture: null,
  };

  if(props.texture) material.texture = getTexture(props.texture);
  material.defaultTexture = getTexture();
  //console.log(props, props.texture, material, textures);

  // load in font
  //const font = new FontLoader().parse(fontRobotoRegular);
  const font = new FontLoader().parse(fontOptimerRegular);

  // configure font mesh
  const textOptions = {
    font,
    size: props.size || 2,
    height: active ? 0 : 1
  };

  return (
    <mesh
      {...props}
      ref={mesh}
      onClick={e => setActive(!active)}
      onPointerOver={(e) => setHover(true)}
      onPointerOut={(e) => setHover(false)}
    >
        {//@ts-ignore
        <textGeometry attach="geometry" args={[props.text, textOptions]} />
        }
        {material.texture?
        <meshStandardMaterial attach="material" color={material.color || 'gold'} args={[{ map: material.texture }]} metalnessMap={material.texture} metalness={0.8} normalMap={material.texture} />:
        <meshStandardMaterial attach="material" color={material.color || 'gold'} args={[{ map: material.defaultTexture }]} metalness={0.4} />
        }
    </mesh>
  );
}

export const TexturedPlane = ({texture, size, elevation, depthTest = true}: SceneProps) => {
  const color = 'gold';
  const [active, setActive] = useState(false);

  // useThree(({camera}: any) => {
  //   console.log(camera);
  // });

  const textureMaterial = getTexture(texture);

  return <mesh scale={[size, size, 1]} rotation={[degToRad(-90), 0, 0]} position={[0, elevation, 0,]} onClick={()=>setActive(!active)}>
      <planeGeometry attach="geometry" args={[1, 1, 50, 50]} />
      <meshPhongMaterial attach="material" color={color} map={textureMaterial} side={DoubleSide} depthTest={depthTest} />
  </mesh>
}
