import React from "react";
import { LiquidGold4KScene } from "./scene_gold";
import { WhiteScene } from "./scene_white";

export function getScene(scene: string) {
    scene = (scene || '').replace(/\W+/g, '');

    switch(scene) {
        case 'gold':
        case 'liquidgold':
            return <LiquidGold4KScene size={1000} />;
        case 'gold4k':
        case 'liquidgold4k':
            return <LiquidGold4KScene />;
        case 'white':
        case 'wireframes':
            return <WhiteScene />;
        default:
            return <LiquidGold4KScene />;
    }
}
